#header-container {
    display: grid;
    grid-template-rows: 10% 80% 10%;
    grid-template-columns: 10% 70% 20%;
    width: 100%;
    height: 10%;
    background-color: slateblue;
}

.sidebar {
    grid-row: 2/3;
    grid-column: 0/3;
}

.contact {
    grid-row: 2/3;
    grid-column: 3/3;
    background-color: transparent;
}